.header {
    width: 100%;
    position: relative;
    min-height: 90px;
    background: linear-gradient(#6765d3, #a7c4ff);
    border-bottom: solid 5px #ff6d72;

    display: flex;
    justify-content: space-between;
	align-items: center;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);

    padding: 0 var(--col-size);

    &__logo {
        padding-top: 2em;
        border-left: solid 5px #ffffff;
        padding-left: 1em;
    }

    &__link {
        display: inline-block;
        text-transform: uppercase;
        margin-right: 2em;
        color: #ffffff;
        font-size: 90%;
        font-weight: bold;

        &:hover {
            text-decoration: none;
        }
    }

    &__githab {
        &:hover {
            svg {
                filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.5));
            }
        }
    }
}

@media screen and (max-width: 767px) {
    .header {
        flex-wrap: wrap;

        &__logo {
            svg {
                width: 240px;
            }
        }

        &__right {
            margin-top: 1em;
        }

        &__githab {
            display: none;
        }
    }
}


@media screen and (max-width: 540px) {
    .header {
		padding: 0 2.5%;
    }
}
