.started {
    width: calc(var(--col-size) * 8);
    margin: 2em auto;

    line-height: 1.8;

    h2 {
        font-size: 110%;
        margin: 2em 0 0.5em 0;
        border-bottom: solid 1px #eee;
        border-left: solid 5px #6765d3;
        padding-left: 10px;
    }
}

.started__header {
    font-size: 150%;
    padding-bottom: 0.5em;
    border-bottom: solid 1px #eee;
    margin: 0;
}

.demoBlock {
    margin: 2em 0 3em 0;
    background-color: #f9f9f9;
    position: relative;
    overflow: hidden;
    padding: 1em;
    color: #444444;

    &__action {
        display: block;
        position: absolute;
        right: 1em;
        top: 1em;
        border: none;
        background: none;
        text-transform: uppercase;
        font-size: 65%;
        transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        border-radius: 3px;
        color: #ffffff;
        padding: 0.3em 1em;
        font-weight: bold;
        cursor: pointer;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
        background-color: rgba($color: #e88181, $alpha: 0.7);
        z-index: 10;

        &:hover {
            background-color: rgba($color: #e88181, $alpha: 0.9);
        }
    }

    &__source {
        display: none;
    }

    &.--source {
        padding: 0;

        .demoBlock__footer {
            margin-bottom: 0em;
            margin-left: 0.5em;
        }

        .demoBlock__data {
            display: none;
        }

        .demoBlock__source {
            display: block;

            * {
                font-size: 90%;
                background-color: #f9f9f9 !important;
            }
        }
    }
}

.demoBlock__footer {
    text-align: left;
    margin-bottom: -1em;
    margin-left: -0.5em;

    svg {
        width: 30px;
        height: 30px;
        opacity: 0.5;

        &:hover {
            opacity: 1;
        }
    }
}

.demoContent {
    padding: 0 0 0.6em 0;
    margin: 0 0 0.6em 0;
    border-bottom: solid 1px #eee;
}

.funcName {
    color: #e88181;
}

.demoTitle {
    font-weight: 600;
    text-transform: uppercase;
    opacity: 0.8;
    font-size: 80%;
}

.demoList {
    margin: 0;

    li {
        padding: 5px 0;
    }
}

@media screen and (max-width: 1100px) {
    .started {
        width: calc(var(--col-size) * 10);

        h2 {
            margin: 1.5em 0 0.5em 0;
        }
    }
}

@media screen and (max-width: 540px) {
    .started {
        width: 95%;
    }
}

@media screen and (max-width: 767px) {
    .started {
    }
}
