.content {
    display: flex;
    flex-direction: column;
	justify-content: stretch;

    label {
        margin: 0.5em 0;
		display: block;
		flex: 1;
		
		span {
			text-transform: uppercase;
			font-size: 70%;
			color: #6b6b6b;
			font-weight: bold;
		}
    }

    input {
		display: block;
		width: 100%;
        padding: 0.9em;
        border: solid 1px #eee;
        border-radius: 5px;
        font-size: 80%;
        color: #6b6b6b;
    }
}

.errorPlace {
	flex: 1;
	font-size: 65%;
	text-transform: uppercase;
	font-weight: bold;
	color: #c94c4c;
}
