.footer {
    width: 100%;
    position: relative;
    min-height: 90px;
    background: linear-gradient(#a7c4ff, #6765d3);
    border-top: solid 5px #ff6d72;

    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.4);

    padding: 0 var(--col-size);

    font-size: 95%;
    color: #ffffff;
    * {
        color: #ffffff;
    }
}

@media screen and (max-width: 767px) {
    .footer {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 540px) {
    .footer {
        padding: 0 2.5%;
    }
}
