.intro {
    width: calc(var(--col-size) * 8);
    margin: 2em auto;

    display: flex;
	align-items: space-between;
	flex-wrap: wrap;

    &__text {
        flex: 1;
		width: 60%;
		min-width: 350px;
		padding-right: 1em;
		
		p {
			padding-bottom: 1.5em;
			font-size: 90%;
			line-height: 1.5;
		}

		ul {
			margin: 0;
			padding: 0;
			font-size: 90%;

			li {
				display: block;
				margin: 0 0 0 2em;
				padding: 0.5em 0.5em;
				border-bottom: solid 1px #f3f3f3;
			}
		}
    }

    &__code {
        flex: 1;
		width: 40%;
		min-width: 350px;
        position: relative;

        > pre {
            box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
        }

        &:hover {
            .ic__run {
                opacity: 1;
            }
        }
    }
}

.ic__run {
    position: absolute;
    right: 1em;
    top: 1em;
    cursor: pointer;
    opacity: 0.5;
    transition: opacity 0.5s ease-in-out;

    svg {
        width: 30px;
        height: 30px;
    }
}

.ic__resultDialog {
	padding: 1em;
	font-size: 80%;
	font-style: italic;

	span {
		text-transform: uppercase;
		font-style: normal;
		font-weight: 600;
		font-size: 100%;
	}
}

@media screen and (max-width: 1100px) {
    .intro {
        width: calc(var(--col-size) * 10);
    }
}

@media screen and (max-width: 540px) {
    .intro {
		width: 95%;
    }
}

@media screen and (max-width: 767px) {
    .intro {
		&__text {
			min-width: 300px;
		}

		&__code {
			margin-top: 1em;
			min-width: 300px;
		}
    }
}
