.exampleBlock {
    margin: 2em auto;
    width: calc(var(--col-size) * 8);
}

.example {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background-color: rgba($color: #f3f3f3, $alpha: 0.5);
    padding: 1em;
    border-radius: 10px;
    margin-bottom: 2em;
}

.customDialog {
    label {
        text-transform: uppercase;
        font-size: 70%;
        font-weight: bold;
        color: #7a7a7a;

        input {
            width: 100%;
            margin: 5px 0;
            padding: 10px;
            border-radius: 5px;
            border: solid 1px #eeeeee;
        }
    }
}

.exampleLeft {
    width: 35%;
    display: flex;
    justify-content: center;
    align-items: center;

    .exampleItem {
		margin: 0.3em 0 0.6em 0;
        display: flex;
        flex-direction: column;

        justify-content: center;
        align-items: center;
    }

    .exampleResult {
        text-align: center;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 70%;
        padding-top: 10px;
        color: #7a7a7a;
    }
}

.exampleRight {
    width: 65%;

    .code {
        position: relative;

        border-radius: 10px;
        box-shadow: 0 3px 4px rgba(0, 0, 0, 0.1);

        * {
            font-size: 85%;
            line-height: 1.2 !important;
        }

        .codeHide {
            position: absolute;
            right: 1em;
            top: 1em;
            display: block;
            text-transform: uppercase;
            font-size: 65%;
            font-weight: bold;
            color: #ffffff;
            text-transform: uppercase;
            padding: 0.5em;
            cursor: pointer;

			transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
			border-radius: 3px;
            background-color: rgba($color: #e88181, $alpha: 0.7);

            &:hover {
                background-color: rgba($color: #e88181, $alpha: 0.9);
            }
        }

        transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);

        &.--minimized {
            border: none;
            max-height: 80px;
            overflow-y: hidden;
            box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
            opacity: 0.7;
            cursor: pointer;

            > * {
                margin-top: -5px !important;
            }

            &::before {
                content: 'Show source';
                position: absolute;
                right: 1em;
				top: 1em;
				border-radius: 3px;
                display: block;
                text-transform: uppercase;
                font-size: 60%;
                font-weight: bold;
                color: #ffffff;
                padding: 0.5em;
                background-color: rgba($color: #e88181, $alpha: 0.7);
            }

            &::after {
                content: '';
                position: absolute;
                display: block;
                left: 0;
                bottom: 0;
                height: 80px;
                width: 100%;
                transition: opacity 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                background: linear-gradient(rgba(255, 255, 255, 0), (rgba(255, 255, 255, 0.95)));
            }

            &:hover {
                opacity: 0.8;
                box-shadow: 0 3px 4px rgba(0, 0, 0, 0.2);

                &::after {
                    opacity: 0.3;
                }
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    .exampleBlock {
        width: calc(var(--col-size) * 10);
    }
}

@media screen and (max-width: 540px) {
    .exampleBlock {
        width: 95%;
    }
}

@media screen and (max-width: 900px) {
    .example {
        flex-wrap: wrap;
    }

    .exampleLeft {
        width: 100%;
    }

    .exampleRight {
        width: 100%;
    }
}
